import { Component, Input, OnInit } from '@angular/core';
import { Account } from '../../../services/wallet/wallet';
import { TimeAgoPipe } from '../../../pipes/time-ago.pipe';
import { TranslateService } from '@ngx-translate/core';
import { MessageService } from '../../../services/message/message.service';
import { TokenService } from '../../../services/token/token.service';
import { CONSTANTS } from '../../../../environments/environment';
import { decode } from 'blurhash';
import { WalletService } from '../../../services/wallet/wallet.service';

@Component({
  selector: 'app-balances',
  templateUrl: './balances.component.html',
  styleUrls: ['../../../../scss/components/account-view/cards/balances.component.scss'],
})
export class BalancesComponent implements OnInit {
  Object = Object;
  @Input() account: Account;
  @Input() activity: any;
  contractAliases = CONSTANTS.CONTRACT_ALIASES;
  contracts = {};

  // dummyData = [
  //   { icon: '', title: "Ethereum", price: "3.0000", priceUSD: "330.03" },
  //   { icon: '', title: "Dogecoin", price: "1094.0000", priceUSD: "75.03" },
  //   { icon: '', title: "Litecoin", price: "136.0000", priceUSD: "10000.03" },
  //   { icon: '', title: "Bitcoin", price: "4256.0000", priceUSD: "50000.03" }
  // ];

  constructor(
    public translate: TranslateService,
    public messageService: MessageService,
    public timeAgoPipe: TimeAgoPipe,
    public tokenService: TokenService,
    public walletService: WalletService
  ) {
    this.walletService.walletUpdated.subscribe(() => {
      if (!!this.account) {
        this.contracts = {
          tezos: { thumbnailUrl: '', symbol: 'Tezos', price: this.account.balanceXTZ, priceUSD: this.account.balanceUSD }
        };
        this.parseMeta();
      }
    });
  }
  ngOnInit(): void {
    this.contracts = {
      tezos: { thumbnailUrl: '', symbol: 'Tezos', price: this.account.balanceXTZ, priceUSD: this.account.balanceUSD }
    };
    this.parseMeta();
  }

  parseMeta() {
    this.tokenService.knownTokenIds().forEach((tokenId) => {
      const contractAddress = tokenId.substring(0, tokenId.indexOf(':'));
      const asset = this.tokenService.getAsset(tokenId);
      if (!asset?.symbol.length) { return; }
      if (!this.contracts[contractAddress]) {
        this.contracts[contractAddress] = [];
        let name: string | string[] = Object.keys(this.contractAliases).filter(key => this.contractAliases[key].address.find((a) => a === contractAddress));
        name = !!name?.length ? name[0] : contractAddress;
        let thumbnailUrl = this.contractAliases[name]?.thumbnailUrl;
        if (!thumbnailUrl) {
          const pixels = decode(contractAddress.slice(0, 22), 700, 700, 2);
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
          const imageData = ctx.createImageData(700, 700);
          imageData.data.set(pixels);
          ctx.putImageData(imageData, 0, 0);
          thumbnailUrl = canvas.toDataURL();
        }
        this.contracts[contractAddress] = asset;
      }
    });
  }
}

