import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges, OnChanges } from '@angular/core';
import { TemplateRequest, TemplateFee, FullyPreparedTransaction } from '../../../send/interfaces';
import { Template, BaseTemplate } from 'kukai-embed';
import { ModalComponent } from '../../modal.component';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { filter } from 'rxjs/operators';
import { WalletService } from '../../../../services/wallet/wallet.service';

@Component({
  selector: 'app-token-detail',
  templateUrl: './token-detail.component.html',
  styleUrls: ['../../../../../scss/components/send/send.component.scss']
})
export class TokenDetail extends ModalComponent implements OnInit {
  Object = Object;
  token = null;
  blacklistMeta = ['category', 'decimals', 'id', 'isBooleanAmount', 'isTransferable', 'kind', 'thumbnailUrl'];
  activeAccount = null;

  name = 'token-detail';

  constructor(
    private route: ActivatedRoute,
    private walletService: WalletService,
    private router: Router
  ) { super(); }

  ngOnInit(): void {
    this.walletService.activeAccount.subscribe(activeAccount => {
      this.activeAccount = activeAccount;
    });
  }

  open(data) {
    this.token = data;
    super.open();
  }
}

