import { Component, Input, OnInit } from '@angular/core';
import { Account, ImplicitAccount } from '../../../services/wallet/wallet';
import { TranslateService } from '@ngx-translate/core';
import { MessageService } from '../../../services/message/message.service';
import { TokenService } from '../../../services/token/token.service';
import { CONSTANTS } from '../../../../environments/environment';
import { decode } from 'blurhash';
import { ModalComponent } from '../../modal/modal.component';
import { WalletService } from '../../../services/wallet/wallet.service';

@Component({
  selector: 'app-nfts',
  templateUrl: './nfts.component.html',
  styleUrls: ['../../../../scss/components/account-view/cards/nfts.component.scss'],
})
export class NftsComponent implements OnInit {
  Object = Object;
  contracts = {};
  filter = 'ALL';
  contractAliases = CONSTANTS.CONTRACT_ALIASES;
  constructor(
    public translate: TranslateService,
    public messageService: MessageService,
    public tokenService: TokenService,
    private walletService: WalletService
  ) { }
  trigger = true;
  @Input() activity: any;
  @Input() account: Account;
  ngOnInit(): void {
    this.walletService.activeAccount.subscribe(activeAccount => {
      this.account = activeAccount;
      this.parseMeta();
    });
    this.tokenService.metadataUpdated.subscribe(() => this.parseMeta());
  }
  displayTokenCard(): boolean {
    return (this.account instanceof ImplicitAccount) || (this.account?.tokens?.length > 0);
  }
  toggleDropdown(sel) {
    const elem = [].slice.call(document.querySelectorAll(`.nfts .collection`));
    const c = document.querySelector(sel).classList.contains('expanded');
    elem.forEach(token => { if (token.classList.contains('expanded')) { token.classList.toggle('expanded'); token.querySelector('.body').scrollTop = 0; } });
    if (!c) { document.querySelector(sel).classList.toggle('expanded'); }
  }
  parseMeta() {
    this.contracts = {};
    this.account.tokens.map(token => {
      const contractAddress = token.tokenId.substring(0, token.tokenId.indexOf(':'));
      const asset = this.tokenService.getAsset(token.tokenId);
      if (!!asset?.symbol) { return; }
      if (!this.contracts[contractAddress]) {
        this.contracts[contractAddress] = [];
        let name: string | string[] = Object.keys(this.contractAliases).filter(key => this.contractAliases[key].address.find((a) => a === contractAddress));
        name = !!name?.length ? name[0] : contractAddress;
        let thumbnailUrl = this.contractAliases[name]?.thumbnailUrl;
        if (!thumbnailUrl) {
          const pixels = decode(contractAddress.slice(0, 22), 700, 700, 2);
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
          const imageData = ctx.createImageData(700, 700);
          imageData.data.set(pixels);
          ctx.putImageData(imageData, 0, 0);
          thumbnailUrl = canvas.toDataURL();
        }
        const d = { name, thumbnailUrl, tokens: [] };
        this.contracts[contractAddress] = d;
      }
      this.contracts[contractAddress].tokens.push(asset);
    });
  }

  viewToken(token) {
    ModalComponent.currentModel.next({ name: 'token-detail', data: token });
  }
}

