import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subject } from 'rxjs';
import { debounceTime, filter } from 'rxjs/operators';
import { WalletService } from '../../services/wallet/wallet.service';
import { DelegateService } from '../../services/delegate/delegate.service';
import { ModalComponent } from '../modal/modal.component';


@Component({
  selector: 'app-delegate-list',
  templateUrl: './delegate.component.html',
  styleUrls: ['../../../scss/components/delegate/delegate.component.scss']
})
export class DelegateListComponent implements OnInit {
  delegates = [];
  filteredDelegates = [];
  toPkh = '';
  searchChanged: Subject<any> = new Subject<any>();
  activeAccount = null;

  constructor(
    private delegateService: DelegateService,
    public router: Router,
    public walletService: WalletService
  ) {
    this.searchChanged.pipe(debounceTime(500)).subscribe(event => {
      const filter = new RegExp(event.target.value, 'i');
      this.filteredDelegates = this.delegates.filter(d => filter.test(d.name) || filter.test(d.fee));
    });
  }

  async ngOnInit() {

    this.walletService.activeAccount.subscribe(activeAccount => {
      this.activeAccount = activeAccount;
    });

    this.filteredDelegates = this.delegates = (await this.delegateService.getDelegates());
  }

  stake(address, fee) {
    ModalComponent.currentModel.next({ name: 'delegate-confirm', data: { address, fee } });
  }

  nameSearch(event) {
    this.searchChanged.next(event);
  }
}
