import { Component, OnInit, Input } from '@angular/core';
import { Account } from '../../services/wallet/wallet';
import { TokenService } from '../../services/token/token.service';

@Component({
  selector: 'app-send-button',
  templateUrl: './send-button.component.html',
  styleUrls: ['../../../scss/components/send/send.component.scss']
})

export class SendButtonComponent implements OnInit {
  @Input() activeAccount: Account;
  @Input() tokenTransfer: string;
  constructor(
    public tokenService: TokenService,
  ) { }

  ngOnInit() {
  }
  prepareTransaction() {
    this.tokenService.prepareTokenTransfer.next({ account: this.activeAccount, tokenTransfer: this.tokenTransfer });
  }
}
